//@ts-ignore
import createError from 'axios/lib/core/createError';
import { AxiosResponse, AxiosRequestConfig } from 'axios';
interface MpResponse extends WechatMiniprogram.RequestSuccessCallbackResult {
  /**  */
  headers: WechatMiniprogram.IAnyObject;
  status: number;
}

interface MpRequestConfig extends WechatMiniprogram.RequestOption {
  /**  */
  headers?: WechatMiniprogram.IAnyObject;
}

const enum EnumPlatForm {
  weixin = 'wexin',
  zhifubao = 'alipay',
  baidu = 'baidu',
  dingding = 'dd',
}

let platFormName: EnumPlatForm = EnumPlatForm.weixin;

/**
 * 
 */
export function getRequest(): (
  option: WechatMiniprogram.RequestOption
) => WechatMiniprogram.RequestTask {
  switch (true) {
    case typeof wx === 'object':
      platFormName = EnumPlatForm.weixin;
      return wx.request.bind(wx);
    case typeof swan === 'object':
      platFormName = EnumPlatForm.baidu;
      return swan.request.bind(swan);
    case typeof dd === 'object':
      platFormName = EnumPlatForm.dingding;
      // https://open.dingtalk.com/document/orgapp-client/send-network-requests
      return dd.httpRequest.bind(dd);
    case typeof my === 'object':
      /**
       * remark:
       *  my.httpRequest my.request my.request my.httpRequest
       * my.httpRequest{'content-type': 'application/x-www-form-urlencoded'}
       * my.request{'content-type': 'application/json'}
       *  dd.httpRequest
       */
      platFormName = EnumPlatForm.zhifubao;
      return (my.request || my.httpRequest).bind(my);
    default:
      return wx.request.bind(wx);
  }
}

/**
 * 
 * @param mpResponse
 * @param config axios
 * @param request api
 */
export function transformResponse(
  mpResponse: MpResponse,
  config: AxiosRequestConfig,
  mpRequestOption: WechatMiniprogram.RequestOption
): AxiosResponse {
  const headers = mpResponse.header || mpResponse.headers;
  const status = mpResponse.statusCode || mpResponse.status;

  let statusText = '';
  if (status === 200) {
    statusText = 'OK';
  } else if (status === 400) {
    statusText = 'Bad Request';
  }

  const response: AxiosResponse = {
    data: mpResponse.data,
    status,
    statusText,
    headers,
    config,
    request: mpRequestOption,
  };
  return response;
}

/**
 * 
 * @param error api
 * @param reject promise reject 
 * @param config
 */
export function transformError(error: any, reject: any, config: any) {
  switch (platFormName) {
    case EnumPlatForm.weixin:
      if (error.errMsg.indexOf('request:fail abort') !== -1) {
        // Handle request cancellation (as opposed to a manual cancellation)
        reject(createError('Request aborted', config, 'ECONNABORTED', ''));
      } else if (error.errMsg.indexOf('timeout') !== -1) {
        // timeout
        reject(
          createError(
            'timeout of ' + config.timeout + 'ms exceeded',
            config,
            'ECONNABORTED',
            ''
          )
        );
      } else {
        // NetWordError
        reject(createError('Network Error', config, null, ''));
      }
      break;
    case EnumPlatForm.dingding:
    case EnumPlatForm.zhifubao:
      // https://docs.alipay.com/mini/api/network
      if ([14, 19].includes(error.error)) {
        reject(
          createError('Request aborted', config, 'ECONNABORTED', '', error)
        );
      } else if ([13].includes(error.error)) {
        // timeout
        reject(
          createError(
            'timeout of ' + config.timeout + 'ms exceeded',
            config,
            'ECONNABORTED',
            '',
            error
          )
        );
      } else {
        // NetWordError
        reject(createError('Network Error', config, null, '', error));
      }
      break;
    case EnumPlatForm.baidu:
      // TODO error.errCode
      reject(createError('Network Error', config, null, ''));
      break;
  }
}

/**
 * axiosconfig
 * @param config
 */
export function transformConfig(config: MpRequestConfig): any {
  if ([EnumPlatForm.zhifubao, EnumPlatForm.dingding].includes(platFormName)) {
    config.headers = config.header;
    delete config.header;
    if (
      EnumPlatForm.dingding === platFormName &&
      config.method !== 'GET' &&
      config.headers?.['Content-Type'] === 'application/json' &&
      Object.prototype.toString.call(config.data) === '[object Object]'
    ) {
      // Content-Typeapplication/jsondatajsonJSON.stringify
      config.data = JSON.stringify(config.data);
    }
  }
  return config;
}
